import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
//Images
import athlete from '../img/athlete-small.png';
import goodtimes from '../img/goodtimes-small.png';
import theracer from '../img/theracer-small.png';
// import animations
import {motion} from 'framer-motion';
import {pageAnimation, fade, photoAnimation, lineAnimation, slider, sliderContainer} from '../animation';
import {ScrollTop} from '../components/ScrollTop';

//my images
import game from '../img/game.jpeg'
import proTrader from '../img/Protrader.png'

//import animation 
import { useScroll } from '../components/useScroll';

const OurWork = () => {
    const [element, controls] = useScroll();
    const [element2, controls2] = useScroll();
    const [element3, controls3] = useScroll();
    return(
        <Work style={{ background:"#fff"}} exit="exit" variants={pageAnimation} initial="hidden" animate="show">
            <motion.div variants={sliderContainer}>
                <Frame1 variants={slider}></Frame1>
                <Frame2 variants={slider}></Frame2>
                <Frame3 variants={slider}></Frame3>
                <Frame4 variants={slider}></Frame4>
            </motion.div>
            <Movie variants={fade} transition={{delay: 0.5}}>
                <motion.h2 variants={fade}>Guesss the opinion game</motion.h2>
                <motion.div variants={lineAnimation} className='line'></motion.div>
                <Link to="/work/Guess-the-Opinion">
                    <Hide>
                        <motion.img variants={photoAnimation} src={game} alt="athlete" />
                    </Hide>
                </Link>
            </Movie>
            <Movie ref={element2} variants={fade} animate={controls2} initial="hidden">
                <motion.h2>Pro Trader</motion.h2>
                <motion.div variants={lineAnimation} className='line'></motion.div>
                <Link to='/work/Pro-Trader'>
                <Hide>
                    <motion.img variants={photoAnimation} src={proTrader} alt="athlete" />
                </Hide>
                </Link>
            </Movie>
            <Movie ref={element3} variants={fade} animate={controls3} initial="hidden">
                <motion.h2>The Racer</motion.h2>
                <motion.div variants={lineAnimation} className='line'></motion.div>
                <Link>
                <Hide>
                <motion.img variants={photoAnimation} src={theracer} alt="athlete" />
                </Hide>
                </Link>
            </Movie>
            <ScrollTop />
        </Work>
    )
};

const Work = styled(motion.div)`
    min-height: 100vh;
    overflow: hidden;
    padding: 5rem 10rem;
    h2{
        padding: 1rem 0rem;
    }
    @media (max-width: 1300px){
        padding: 2rem 2rem;
    }
`
const Movie = styled(motion.div)`
    padding-bottom: 10rem;
    .line{
        height: 0.5rem;
        background: #23d997;
        margin-bottom:3rem ;
    }
    img{
        width:100% ;
        height: 70vh;
        object-fit: cover;

    }
`

const Hide = styled.div`
    overflow: hidden;
`

//FrameAnimation 
const Frame1 = styled(motion.div)`
    position: fixed;
    left: 0;
    top: 10%;
    width: 100%;
    height: 100vh;
    background:#fffebf ;
    z-index:2;
`
const Frame2 = styled(Frame1)`
    background: #ff8efb;
`

const Frame3 = styled(Frame1)`
    background: #8ed2ff;
`
const Frame4 = styled(Frame1)`
    background: #8effa0;
`

export default OurWork;